import React from "react";
import {
  Box,
  Center,
  Flex,
  Text,
  Link,
  VStack,
} from "@chakra-ui/react";
import { Link as RouterLink } from "react-router-dom";

function SideNavigation() {
  return (
    <Box overflowY="auto" as="nav" height="100%" borderColor="teal.500" borderRightWidth="2px">
      <VStack width="270px"
              paddingY="30px" paddingLeft="20px" spacing="4" alignItems="start">
        <Box>
          <Text fontSize="xl">
            <RouterLink to="/">Aktuelles</RouterLink>
          </Text>
          <Text fontSize="l" ml="2">
            <RouterLink to="/trainingsort-zeit">Trainingsort und -zeit</RouterLink>
          </Text>
          <Text fontSize="l" ml="2">
            <RouterLink to="/schnupper-abend">Schupperabende</RouterLink>
          </Text>
          <Text fontSize="l" ml="2" color="gray.300">
            Berichte
          </Text>
          <Text fontSize="l" ml="2" color="gray.300">
            Archiv
          </Text>
        </Box>
        <Box>
          <Text fontSize="xl">
            <RouterLink to="/was-ist-kyudo">Was ist Kyudo</RouterLink>
          </Text>
          <Text fontSize="l" ml="2">
            <RouterLink to="/training">Training</RouterLink>
          </Text>
          <Text fontSize="l" ml="2" color="gray.300">
            Ausrüstung
          </Text>
          <Text fontSize="l" ml="2">
            <RouterLink to="/links">Links</RouterLink>
          </Text>
          <Text fontSize="l" ml="2" color="gray.300">
            Lexikon
          </Text>
        </Box>
        <Box>
          <Text fontSize="xl">
          <RouterLink to="/verein">Verein</RouterLink>
          </Text>
          <Text fontSize="l" ml="2" color="gray.300">
            Vorstand
          </Text>
          <Text fontSize="l" ml="2">
            <RouterLink to="/termine">Termine im Dojo</RouterLink>
          </Text>
          <Text fontSize="l" ml="2" color="gray.300">
            Formulare
          </Text>
        </Box>
        <Box>
          <Text fontSize="xl">
            <RouterLink to="/kontakt">Kontakt</RouterLink>
          </Text>
          <Text fontSize="l" ml="2" color="gray.300">
            Virtuelles Dojo
          </Text>
        </Box>
        <Box>
          <Text fontSize="xl">
            <RouterLink to="/impressum">Impressum</RouterLink>
          </Text>
        </Box>
      </VStack>
    </Box>
  )
}

export default SideNavigation