import React from "react";
import {
  Text,
  Center,
  Image,
  Box,
  VStack
} from "@chakra-ui/react";

function Home() {

  return (
    <VStack spacing="8">
      <Center>
        <Text fontSize="2xl" textAlign="center">
          Herzlich Willkommen im Silbersee-Dojo,
          <br/>
          Kyudo-Verein Hasbergen e.V.
        </Text>
      </Center>
      <Center>
        <Image src="./kniender-kyudoka.gif" alt="test" px="6" width="full" maxWidth="400px" objectFit="contain"/>
      </Center>
    </VStack>
  )
};

export default Home;