import React from "react";
import {
  Text,
  Center,
  Image,
  Box,
  VStack,
  Link
} from "@chakra-ui/react";
import { EmailIcon } from "@chakra-ui/icons";

function Kontakt() {
  return (
    <VStack spacing="8" alignItems="start" mx="4">
      <Box>
        <Text fontSize="2xl">
          Ansprechpartner
        </Text>
        <Text>
          Wer auch immer
          <br/>
          In wo auch immer
        </Text>
        <Link href="mailto:info@kyudo-hasbergen.de" isExternal>
          <EmailIcon mr="2px"/>info@kyudo-hasbergen.de
        </Link>
      </Box>
      <Box>
        <Text fontSize="2xl">
          Redaktion
        </Text>
        <Text>
          Wer auch immer
          <br/>
          In wo auch immer
        </Text>
        <Link href="mailto:info@kyudo-hasbergen.de" isExternal>
          <EmailIcon mr="2px"/>webmaster@kyudo-hasbergen.de
        </Link>
      </Box>
    </VStack>
  )
};

export default Kontakt;