import React from "react";
import {
  Box,
  Divider,
  Link,
  ListItem,
  Text,
  UnorderedList,
  VStack,
} from "@chakra-ui/react";
import { ExternalLinkIcon } from "@chakra-ui/icons";

function SchnupperAbend() {
  return (
    <VStack spacing="2" alignItems="start">
      <Text>
        Haben Sie Interesse an unserem schönen Sport?
      </Text>
      <Text>
        Unverbindlich beim nächsten Schnupperabend vorbeischauen,
        beim Schießen zusehen und etwas über die Geschichte und die Technik des Kyudos hören.
      </Text>
      <Text>
        Termine:
      </Text>
      <VStack pl="4" mt="4" spacing="1" alignItems="start">
        <Text>Dienstag, 27. April 2021</Text>
        <Text>Dienstag, 22. Juni 2021</Text>
        <Text>
          Sonntag, 12.September 2021 ab 15.00 Uhr
          <br/>
          (Tag des offenen Denkmals auf Gösmann's Hof)
        </Text>
      </VStack>
    </VStack>
  )
}

export default SchnupperAbend;